.video {
  background-color: $very-dark-blue;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 706px;

  h2 {
    @include title;
    margin-top: 120px;
    line-height: 30px;
    padding-right: 10px;
  }

  p {
    margin: 0;
    margin-top: 20px;
    opacity: 0.7;
    max-width: 340px;
    font-weight: 300;
    line-height: 28px;
    text-align: center;
  }

  @media (max-width: $bp-tablet) {
    min-height: 413px;

    h2 {
      font-size: 24px;
      margin-top: 70px;
      padding-right: 0;
    }

    p {
      max-width: 430px;
    }
  }

  @media (max-width: $bp-mobile) {
    min-height: 384px;

    h2 {
      margin-top: 85px;
      padding-right: 9px;
    }
  }
}

.video__inner {
  position: relative;
  width: 100%;
  // display: flex;
  // justify-content: center;
  margin-top: 56px;
  height: 420px;

  picture {
    position: absolute;
    width: 922px;
    top: 0;
    left: 50%;
    transform: translate(-50%);
  }

  a {
    position: absolute;
    width: 65px;
    height: 65px;
    border-radius: 50%;
    background: $vivid-cyan;
    top: 230px;
    left: 50%;
    transform: translate(-50%);
    transform-origin: left;
    z-index: 2;
    border: 0;
    transition: all 0.3s;
    cursor: pointer;

    &:hover {
      transform: scale(1.2) translate(-50%);
      background-color: lighten($vivid-cyan, 25);
    }

    &:active {
      transform: scale(1) translate(-50%);
      background: $vivid-cyan;
      background-color: darken($vivid-cyan, 15);
    }

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 9px 0 9px 13px;
      border-color: transparent transparent transparent $very-dark-gray;
      transform: translate(-50%, -50%);
    }
  }

  @media (max-width: $bp-tablet) {
    margin-top: 40px;
    height: 182px;

    picture {
      width: 503px;
    }

    a {
      width: 91px;
      height: 91px;
      top: 95px;

      &::after {
        border-width: 12px 0 12px 18px;
      }
    }
  }

  @media (max-width: $bp-mobile) {
    margin-top: 52px;
    height: 115px;

    picture {
      width: 288px;
    }

    a {
      width: 65px;
      height: 65px;
      top: 50px;

      &::after {
        border-width: 9px 0 9px 13px;
      }
    }
  }
}
